import React, {Component} from 'react';
import OrderStatus from './orderStatus';
import MenuItem from './menuItemComponent';
import ConstructBurger from './constructBurgerComponent';
import { MENU } from '../shared/menu';
import { CONDIMENTS } from '../shared/condimentsOld';

const defaultToppingOption = "Regular"

class Menu extends Component {
    constructor() {
        super();
        this.state = {
            selectedFoodCategory: null,
            selectedFoodItem: null,
            currentOrderToppings: {},
            fullOrder: []
        };
      }

    selectCategory(foodCategory) {
        window.scrollTo(0, 0);
        this.setState({selectedFoodCategory: foodCategory});
    }

    selectFood(foodItem) {
        window.scrollTo(0, 0);
        if (foodItem === null){
           this.setState({
                selectedFoodItem: null,
                currentOrderToppings: null
            });
            return;
        }
        
        let toppings = {};
        CONDIMENTS.forEach((item) => {
            if(item.alltheway === true) {
                toppings[item.name] = defaultToppingOption;
            }
            else
            toppings[item.name] = false;
        });

        this.setState({
            selectedFoodItem: foodItem,
            currentOrderToppings: toppings
        }); 
    }

    selectTopping(update, setTopping) {
        if (setTopping === true) {
            setTopping = defaultToppingOption;
        }
        this.setState({
            currentOrderToppings: {...this.state.currentOrderToppings, [update]: setTopping}
        });
    }

    addFoodToMenu() {
        let order = this.state.fullOrder;
        order.push({food: this.state.selectedFoodItem, toppings: this.state.currentOrderToppings});
        this.setState({
            selectedFoodCategory: null,
            selectedFoodItem: null,
            currentOrderToppings: {},
            fullOrder: order
        });
       console.log(this.state.fullOrder);
    }

    orderMenu() {
        let orderMenu = {
            breadCrumb: null,
            title: null,
            menu: null
        }

        if(this.state.selectedFoodItem != null) {


            //This combines the coppigns data with the current toppigns
        //    var TMPcurrentToppings = this.state.currentOrderToppings[0];
        //    var currentToppings = Object.keys(CONDIMENTS).map(function(key) {
        //        var newToppings = CONDIMENTS[key];
        //        newToppings.value = TMPcurrentToppings[CONDIMENTS[key].name];
        //        return newToppings;
        //    });
        //    console.log(currentToppings);
            //End of this

            switch(this.state.selectedFoodCategory) {
                case "Burgers":
                case "Hot Dogs":
                case "Sandwiches":
                    orderMenu.breadCrumb = <span className="backBreadcrumb" onClick={() => this.selectFood(null)}>Back</span>;
                    orderMenu.Title = this.state.selectedFoodItem;
                    orderMenu.menu = <ConstructBurger toppings={this.state.currentOrderToppings} condiments={CONDIMENTS} selectTopping={(update, setTopping) => this.selectTopping(update, setTopping)} />
                    break;
                case "Fries":
                case "Drinks":
                case "Shakes":
                default:
                    orderMenu.breadCrumb = <span className="backBreadcrumb" onClick={() => this.selectFood(null)}>Back</span>;
                    orderMenu.Title = this.state.selectedFoodItem;
                    orderMenu.menu = <div>coming soon</div>;
            }
        }
        else if (this.state.selectedFoodCategory) {
            orderMenu.breadCrumb = <span className="backBreadcrumb" onClick={() => this.selectCategory(null)}>Back</span>;
            orderMenu.Title = this.state.selectedFoodCategory;
            orderMenu.menu = <div className="row">
                {
                    MENU[this.state.selectedFoodCategory].map((item, key) => {
                        return <MenuItem key={key} title={item.name} tooltip={item.description} subtitle={"$" + item.price} thirdtitle={item.calories + " cal"} image={item.image} onClick={() => this.selectFood(item.name)} />
                    })
                }
                </div>
        }
        else {
            orderMenu.breadCrumb = <span className="backBreadcrumb">&nbsp;</span>;
            orderMenu.Title = "What can we get for you?";
            orderMenu.menu = <div className="row">
                    <MenuItem title="Burgers" image="images/cheeseburger.png" onClick={() => this.selectCategory("Burgers")} />
                    <MenuItem title="HotDogs" image="images/cheesehotdog.png" onClick={() => this.selectCategory("Hot Dogs")} />
                    <MenuItem title="Sandwiches" image="images/bltsandwich.png" onClick={() => this.selectCategory("Sandwiches")} />
                    <MenuItem title="Fries" image="images/fries.png" onClick={() => this.selectCategory("Fries")} />
                    <MenuItem title="Drinks" image="images/drinks.png" onClick={() => this.selectCategory("Drinks")} />
                    <MenuItem title="Shakes" image="images/shakes.png" />
                </div>
        }
        
        return <>
            <div className="row">
                <div className="col-12">
                    {orderMenu.breadCrumb}
                    <h3>{orderMenu.Title}</h3>
                </div>
            </div>
            {orderMenu.menu}
        </>
    }

    render() {
        return <div className="container-lg container-fluid">
            <div className="row">
                <div className="col-12 col-lg-8 orderMenu order-lg-1 order-2">
                    {this.orderMenu()}
                </div>
                <div className="col-12 col-lg-4 order-lg-2 order-1">
                    <OrderStatus
                        currentOrderToppings={this.state.currentOrderToppings}
                        condiments={CONDIMENTS}
                        addToOrder={() => this.addFoodToMenu()}
                    />
                </div>
            </div>
        </div>
    }
}

export default Menu;