import React, { Component } from 'react';
import { Collapse, CardImg, Button, CardBody, Card } from 'reactstrap';
import { baseUrl } from '../shared/baseUrl';

const defaultToppingOption = "Regular"

class OrderStatus extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false,
            isSubOpen: [true]
        };
    }

    componentDidMount() {
        let mql = window.matchMedia('(max-width: 992px)');
        const listenerFunc = () => {
            if (mql.matches) {
                this.setState({isOpen: false});
            }
            else {
                this.setState({isOpen: true});
            }
        }
        listenerFunc();
        mql.addListener(listenerFunc);
    }

    displayCurrentToppings(subItemNumber) {
        if (this.props.currentOrderToppings) {
            let returnItem = null;
            let currentOrderToppings = this.props.currentOrderToppings;
            let condiments = this.props.condiments;
            returnItem = Object.keys(currentOrderToppings).map(function(key, index) {
                if (currentOrderToppings[key]) {
                    if (currentOrderToppings[key] === defaultToppingOption) {
                        return <span key={key}><CardImg style={{width: '32px', height: '32px'}} src={baseUrl + condiments[index].image} alt="baconburger" />{key}</span>
                    }
                    else {
                        return <span key={key}><CardImg style={{width: '32px', height: '32px'}} src={baseUrl + condiments[index].image} alt="baconburger" />{currentOrderToppings[key]} {key}</span>
                    }
                }
                return '';
            });
            return <>
                    <Collapse isOpen={this.state.isSubOpen[subItemNumber]}>{returnItem}</Collapse>
                    <Button color="outline" onClick={() => this.setState({isSubOpen: {...this.state.isSubOpen, [subItemNumber]: !this.state.isSubOpen[subItemNumber]}})} >Show Details</Button>
                </>;
        }
        return '';
    }



    render() {

        const toggleButtonText = ((this.state.isOpen) ? "Hide Order" : "View order")
        
        
        return (
            <div className="orderStatus">
                <Collapse isOpen={this.state.isOpen}>
                    <Card>
                    <CardBody>
                        <CardImg style={{width: '32px', height: '32px'}} src={baseUrl + '/images/baconburger.png'} alt="baconburger" />Bacon Burger<br />
                        {this.displayCurrentToppings(0)}
                    </CardBody>
                    <CardBody>
                        <Button color="primary" onClick={this.props.addToOrder}>Add to order</Button>
                    </CardBody>
                    </Card>
                </Collapse>
                <div className="text-right orderStatusToggleButton">
                    <Button color="outline" onMouseDown={e => e.preventDefault()} onClick={() => this.setState({isOpen: !this.state.isOpen})} >{toggleButtonText}</Button>
                </div>
            </div>
        );
    } 
}

export default OrderStatus;